<template>
  <div class="service" id="services">
    <div class="container">
      <div class="service__content" ref="content">
        <div class="service__content_item">
          <div
            class="service__content_item-services"
            v-for="(item, index) in ServiceContent"
            :key="index"
            :class="{ active: item.isActive }"
            @click="serviseCard(index)"
          >
            <div class="line"></div>
            <span class="service__content_item-services-num">{{
              item.num
            }}</span>
            <h3 class="service__content_item-services-title">
              {{ item.title[activeLanguage] }}
            </h3>
            <p
              class="service__content_item-services-descr"
              v-html="item.descr[activeLanguage]"
            ></p>
          </div>
        </div>
        <div class="service__content_item-txt">
          <div
            class="service__content_item-txt-info"
            v-for="(item, index) in ServiceContent"
            :key="index"
            :class="{ active: item.isActive }"
          >
            <h3 class="service__content_item-txt-info-title">
              {{ item.infoTitle[activeLanguage] }}
            </h3>
            <p
              class="service__content_item-txt-info-descr"
              v-html="item.infoDescr[activeLanguage]"
            ></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ServiceContent: [
        {
          num: "1.",
          title: {
            ru: "МЕРЧЕНДАЙЗИНГ",
            uz: `МЕРЧЕНДАЙЗИНГ`,
            en: "МЕРЧЕНДАЙЗИНГ",
          },
          descr: {
            ru: "(merchandising — искусство торговать)  ",
            uz: `(merchandising — искусство торговать)  `,
            en: "",
          },
          infoTitle: {
            ru: "Мерчендайзинг (merchandising — искусство торговать)",
            uz: ``,
            en: "",
          },
          infoDescr: {
            ru: "Это комплекс мероприятий производимых в торговом зале и направленных на продвижение того или иного товара, марки, вида или упаковки. <br><br> Мерчендайзинг способствует стимулированию розничных продаж в торговых точках через привлечение внимания конечных покупателей к определенным маркам или группам товаров без активного участия персонала.",
            uz: ``,
            en: "",
          },
          isActive: true,
        },
        {
          num: "2.",
          title: { ru: "RETAIL AUDIT", uz: `RETAIL AUDIT`, en: "RETAIL AUDIT" },
          descr: {
            ru: "Add a short description of the process section.",
            uz: ``,
            en: "",
          },
          infoTitle: {
            ru: "Retail аудит",
            uz: ``,
            en: "",
          },
          infoDescr: {
            ru: "Мы готовы предоставить вам следующую информацию о вашем товаре из торговых точек: <br><br><b>• Уровень нумерической дистрибуции; <br>• Уровень качественной дистрибуции;<br>• Уровень отсутствия товара в % (Out of stock);<br>• % доли на полке/в категории в торговой точке;<br>• Оценка существующих и потенциальных товарных категорий</b>",
            uz: ``,
            en: "",
          },
          isActive: false,
        },
        {
          num: "3.",
          title: { ru: "CENSUS", uz: `CENSUS`, en: "CENSUS" },
          descr: {
            ru: "Add a short description of the process section.",
            uz: ``,
            en: "",
          },
          infoTitle: {
            ru: "База торговых точек (Census)",
            uz: ``,
            en: "",
          },
          infoDescr: {
            ru: "<b>Мы фиксируем следующие данные о торговых точках: <br><br>• Название (Юридическое, Вывеска),<br>• Местонахождение (Точный фактический адрес с локацией, с ориентирами) <br>• Бизнес торговой точки (Специализация, Тип точки) <br>• Внешнее фото торговой точки.</b> <br><br> Вы также можете <b>скорректировать список</b> по необходимой вам дополнительной информации по торговым точкам. Наличие у вас полной и максимальной информации о торговых точках существенно увеличивает ваши возможности и позиции по отношению к конкурентам.",
            uz: ``,
            en: "",
          },
          isActive: false,
        },
      ],
    };
  },
  methods: {
    serviseCard(index) {
      let toggle = false;
      this.ServiceContent.forEach((item, idx) => {
        if (index == idx && item.isActive) {
          toggle = true;
        }
        if (item.isActive) {
          item.isActive = false;
        }
      });
      if (!toggle) {
        this.ServiceContent[index].isActive = true;
      }
    },
  },
  computed: {
    activeLanguage() {
      return this.$store.getters.getLanguage;
    },
  },
  watch: {
    services() {
      this.ServiceContent = this.ServiceContent;
    },
  },
};
</script>

