          left: 0;
          right: 0;
          margin-left: -30px;
          @media (max-width: 768px) {
            width: 3px;
          }
        }
        &-num {
          @include adaptiv-fontlg(65, 33);
          color: #fff;
          font-family: "Montserrat-semibold";
