          }
        }
      }
      &-txt {
        width: 50%;
        &-info {
          flex-direction: column;
          justify-content: center;
          @include adaptivPaddinglg(120, 0, 0, 0, 60, 0, 0, 0);
          opacity: 0;
          overflow: hidden;
