<template>
  <div class="count" v-intersection="startCount">
    <div class="container">
      <div class="count__content">
        <div
          class="count__content-item"
          v-for="(item, index) in CountItems"
          :key="index"
        >
          <div class="count__content-item-num">
            <div class="count__content-item-num-span">{{ item.count }}</div>
            <div class="count__content-item-num-span1">{{ item.numWord }}</div>
          </div>
          <div
            class="count__content-item-descr"
            v-html="item.descr[activeLanguage]"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      animationDuration: 3000,
      CountItems: [
        {
          result: 50,
          count: 0,
          numWord: "%",
          descr: {
            ru: "исследованного <br> рынка",
            uz: "ИСЛЕДОВАННОГО РЫНКА",
            en: "ИСЛЕДОВАННОГО РЫНКА",
          },
        },
        {
          result: 12,
          count: 0,
          numWord: "K+",
          descr: {
            ru: "Сотрудников <br> способствуют развитию <br> нашего бизнеса ",
            uz: "СотрудникОВ способствуют развитию нашего бизнеса ",
            en: "СотрудникОВ способствуют развитию нашего бизнеса ",
          },
        },
        {
          result: 300,
          count: 0,
          numWord: "K",
          descr: {
            ru: "Люди со всего мира <br> участвуют в наших <br> панелях",
            uz: "ИСЛЕДОВАННОГО РЫНКА",
            en: "ИСЛЕДОВАННОГО РЫНКА",
          },
        },
      ],
    };
  },
  methods: {
    count(item) {
      setTimeout(() => {
        if (item.count < item.result) {
          item.count += 1;
          this.count(item);
        }
      }, this.animationDuration / item.result);
    },
    startCount() {
      this.CountItems.forEach((item) => {
        this.count(item);
      });
    },
  },
  computed: {
    activeLanguage() {
      return this.$store.getters.getLanguage;
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";
.count {
  // max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  background: #3b2c4e;
  @include adaptivPaddinglg(0, 0, 100, 100, 0, 0, 50, 50);
  &__content {
    width: 100%;
    display: flex;
    justify-content: center;
    @media (max-width: 768px) {
      flex-direction: column;
    }
    &-item {
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      align-items: center;
      border-right: 2px solid #ffffff;
      @media(max-width: 768px){
        @include adaptivMarginlg(0,0,100,100,0,0,55,55);
        border: none;
      }
      &:last-child {
        border-right: none;
      }
      &-num {
        @include adaptivWidthLg(250, 160);
        display: flex;
        justify-content: center;
        align-items: center;
        @include adaptiv-fontlg(96, 48);
        @include adaptiv-lineHeightlg(96, 48);
        color: #fff;
        font-family: "Montserrat-medium";
        @media (min-width: 1921px) {
          font-size: 100px;
          line-height: 100px;
        }
        @media (max-width: 768px) {
          @include adaptiv-fontmd(96, 48);
          @include adaptiv-lineHeightmd(96, 48);
        }
        &-span {
          text-align: right;
          @include adaptiv-fontlg(96, 48);
          @include adaptiv-lineHeightlg(96, 48);
          @media (min-width: 1921px) {
            font-size: 100px;
            line-height: 100px;
          }
          @media (max-width: 768px) {
            @include adaptiv-fontmd(96, 48);
            @include adaptiv-lineHeightmd(96, 48);
          }
        }
      }
      &-descr {
        @include adaptiv-fontlg(20, 15);
        @include adaptiv-lineHeightlg(24, 19);
        color: #fff;
        font-family: "Montserrat-thin";
        text-align: center;
        @media (max-width: 768px) {
          @include adaptiv-fontmd(20, 15);
          @include adaptiv-lineHeightmd(24, 19);
        }
      }
    }
  }
}
</style>