<template>
  <div class="question">
    <div class="container">
      <div class="question__content">
        <div class="question__content-box">
          <h3 class="question__content-box-title">
            {{ boxTitle[activeLanguage] }}
          </h3>
          <p class="question__content-box-descr">
            {{ boxDescr[activeLanguage] }}
          </p>
        </div>
        <div class="question__content-form">
          <form
            id="reg-course"
            class="modal__form"
            @submit.prevent
            @submit="sendFormData"
          >
            <input
              type="text"
              placeholder="Ваше имя"
              name="modal-name"
              class="form__input crm-form__input"
              required
              autocomplete="off"
              v-model="modalName"
            />
            <input
              type="tel"
              placeholder="Номер телефона"
              name="modal-tel"
              class="form__input crm-form__input hidden__phone"
              maxlength="17"
              v-model="modalPhone"
              @input="inputNumber"
              @focus="focusNumber"
              @blur="blurNumber"
              required
            />
            <input
              type="text"
              placeholder="Telegram @username"
              name="modal-name"
              class="form__input crm-form__input hidden__phone"
              v-model="modalUserName"
            />
            <button class="modal-btn" v-if="!submitted">Оставить заявку</button>
            <button class="modal-btn-accept" v-if="submitted">
              ВАША ЗАЯВКА ПРИНЯТА ✔
            </button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      submitted: false,
      modalName: "",
      modalPhone: "",
      modalUserName: "",
      boxTitle: { ru: "у вас есть вопросы?", uz: "", en: "" },
      boxDescr: {
        ru: "Наш менеджер обработает вашу заявку и свяжется с вами в ближайшее время ",
        uz: "",
        en: "",
      },
    };
  },
  methods: {
    sendFormData() {
      if (
        this.modalName != "" &&
        this.modalPhone != "" &&
        this.modalPhone.length == 17
      ) {
        const formData = new FormData();
        formData.append("name", this.modalName);
        formData.append("username", this.modalUserName);
        formData.append("contact", this.modalPhone);
        this.$store.dispatch("sendForm", formData).then((response) => {
          this.submitted = true;
          this.modalName = "";
          this.modalPhone = "";
          this.modalUserName = "";
          setTimeout(() => {
            this.submitted = false;
            this.$emit("close");
          }, 2000);
        });
      }
    },
    inputNumber(event) {
      if (this.modalPhone.length < 5) this.modalPhone = "+998 ";
      this.modalPhone = this.modalPhone.replace(/[^+|^\d|^\s]/, "");
      const lengths = [4, 7, 11, 14];
      lengths.forEach((item) => {
        if (this.modalPhone.length == item && event.data) {
          this.modalPhone += " ";
        }
      });
    },
    focusNumber() {
      if (this.modalPhone == "") this.modalPhone = "+998 ";
    },
    blurNumber() {
      if (this.modalPhone == "+998 ") this.modalPhone = "";
    },
    closeModal() {
      this.$emit("close");
    },
  },
  computed: {
    activeLanguage() {
      return this.$store.getters.getLanguage;
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";
.question {
  width: 100%;
  // max-width: 1920px;
  margin: 0 auto;
  background: #2b2135;
  @include adaptivPaddinglg(0, 0, 140, 70, 0, 0, 70, 35);
  @media(max-width: 768px){
    @include adaptivPaddingmd(0, 0, 80, 80, 0, 0, 40, 40);
  }
  &__content {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &-box {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      @include adaptivPaddinglg(0, 0, 45, 40, 0, 0, 23, 20);
      background: #403153;
      box-shadow: 0px 2px 40px rgba(0, 0, 0, 0.04);
      &-title {
        @include adaptiv-fontlg(48, 24);
        @include adaptiv-lineHeightlg(53, 30);
        color: #fff;
        font-family: "Montserrat-semibold";
        text-transform: uppercase;
        @media (max-width: 768px) {
          @include adaptiv-fontmd(48, 20);
          @include adaptiv-lineHeightmd(53, 24);
        }
      }
      &-descr {
        @include adaptiv-fontlg(20, 16);
        @include adaptiv-lineHeightlg(24, 20);
        color: #fff;
        font-family: "Montserrat-regular";
        @media (max-width: 768px) {
          @include adaptiv-fontmd(20, 12);
          @include adaptiv-lineHeightmd(24, 15);
          text-align: center;
        }
      }
    }
    &-form {
      width: 100%;
      background: #322640;
      @include adaptivPaddinglg(50, 50, 100, 120, 25, 25, 50, 60);
      .modal__form {
        display: flex;
        justify-content: center;
        @media(max-width: 768px){
          width: 100%;
          flex-direction: column;
        }
      }
      .modal-btn {
        width: 100%;
        @include adaptivHeightlg(58, 30);
        background: linear-gradient(90deg, #4facfe 0%, #00f2fe 100%);
        color: #fff;
        @include adaptiv-fontlg(18, 11);
        @include adaptiv-lineHeightlg(15, 11);
        font-family: "Montserrat-regular";
        text-transform: uppercase;
        @include adaptivMarginlg(10, 10, 0, 0, 2, 2, 0, 0);
        .button-link {
          color: #303030;
        }
        @media (max-width: 768px) {
          width: 100%;
          @include adaptivHeightmd(80, 40);
          @include adaptiv-fontmd(22, 12);
          @include adaptiv-lineHeightlg(22, 19);
          @include adaptivMarginmd(0, 0, 5, 0, 0, 0, 3, 0);
        }
        @media (min-width: 1921px) {
          font-size: 20px;
        }
      }
      .form__input {
        width: 100%;
        @include adaptivHeightlg(58, 30);
        display: block;
        font-family: "Montserrat-regular";
        font-style: normal;
        font-weight: 300;
        color: #8e8e8e;
        @include adaptiv-fontlg(18, 12);
        @include adaptiv-lineHeightlg(18, 12);
        @include adaptivMarginlg(10, 10, 0, 0, 2, 2, 0, 0);
        @include adaptivPaddinglg(25, 0, 0, 0, 10, 0, 0, 0);
        border: none;
        background: #eeeded;
        @media (max-width: 768px) {
          @include adaptiv-fontmd(22, 12);
          @include adaptiv-lineHeightmd(25, 15);
          @include adaptivHeightmd(80, 40);
          @include adaptivPaddingmd(15, 15, 0, 0, 10, 10, 0, 0);
          @include adaptivMarginmd(0, 0, 5, 5, 0, 0, 3, 3);
        }
      }
    }
  }
}
</style>