<template>
  <div class="navigation">
    <div class="container">
      <div class="navigation__content">
        <router-link
          aria-label=""
          class="logo"
          title="main-page"
          :to="{ name: 'home', params: { lang: $route.params.lang } }"
        >
          <img src="@/assets/logo.png" alt="logo" />
        </router-link>
        <div class="navigation__content_item" :class="{ show: mobileMenu }">
          <button
            v-for="(item, index) in NavLinks"
            :key="index"
            class="navigation__content_item-btn"
            :id="item.id"
            @click="scrollToSection(item.id)"
          >
            {{ item.link[activeLanguage] }}
          </button>
        </div>
        <div class="navigation-list-mobile" :class="{ active: menuOpen }">
          <div class="navigation-list-mobile-list">
            <button
              class="navigation-list-btn"
              v-for="(item, index) in NavLinks"
              :key="index"
              :id="item.id"
              @click="scrollToSection(item.id)"
            >
              {{ item.link[activeLanguage] }}
            </button>
          </div>
        </div>
        <button
          class="menu-burger"
          :class="{ close: menuOpen }"
          @click="openMenu"
        >
          <span class="menu-burger-line"></span>
          <span class="menu-burger-line"></span>
          <span class="menu-burger-line"></span>
        </button>
        <ul>
          <li
            class="
              navigation__content-language-toggler
              navigation__content-language-togglerMob
            "
          >
            <button @click="changeLanguage('ru')">ru</button>
            <button @click="changeLanguage('uz')">uz</button>
            <button @click="changeLanguage('en')">en</button>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mobileMenu: false,
      opened: false,
      mobileCourses: false,
      dropDownOpened: false,
      viewportWidth: window.innerWidth,
      menuOpen: false,
      NavLinks: [
        {
          link: { ru: "Наши услуги", uz: `Bizning`, en: "Our services" },
          id: "#services",
        },
        {
          link: { ru: "O нас", uz: ``, en: "About us" },
          id: "#about-us",
        },
        {
          link: { ru: "Нам доверяют", uz: ``, en: "We are trust" },
          id: "#trust",
        },
        {
          link: { ru: "Контакты", uz: `Kontaktlar`, en: "Contacts" },
          id: "#contacts",
        },
      ],
    };
  },
  components: {},
  methods: {
    openMenu() {
      this.menuOpen = !this.menuOpen;
    },
    goToTop() {
      window.scrollTo(0, 0);
    },
    changeLanguage(language) {
      const lang = language == "ru" ? "" : language;
      this.$router.push({
        name: this.$route.name,
        params: { lang: lang },
      });
      const newLang = language == "ru" || language == "" ? "ru" : language;
      this.$store.dispatch("selectLanguage", newLang);
    },
    scrollTo(sectionId) {
      if (this.menuOpen) {
        this.menuOpen = false;
      }
      const section = document.querySelector(sectionId);
      const scrolDistance = section.offsetTop - 50;
      let distance = window.scrollY;
      const scroll = () => {
        if (scrolDistance > distance + 0.5 || scrolDistance < distance - 0.5) {
          distance -= (distance - scrolDistance) / 15;
          window.scrollTo(0, distance);
          this.timer = setTimeout(() => {
            scroll();
          }, 5);
        } else {
          window.scrollTo(0, scrolDistance);
          stop();
        }
      };
      const stop = () => {
        clearTimeout(this.timer);
        window.removeEventListener("wheel", stop);
      };
      stop();
      window.addEventListener("wheel", stop);
      scroll();
    },

    scrollToSection(sectionId) {
      if (this.$route.name != "home") {
        this.$router.push("/").then(() => {
          setTimeout(() => {
            this.scrollTo(sectionId);
          }, 300);
        });
      } else {
        this.scrollTo(sectionId);
      }
    },
  },
  computed: {
    activeLanguage() {
      return this.$store.getters.getLanguage;
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";
.navigation {
  width: 100%;
  // max-width: 1920px;
  margin: 0 auto;
  background: linear-gradient(
    270deg,
    rgba(25, 60, 180, 0.92) 0.53%,
    #3c31a7 67.81%
  );
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  @media (max-width: 768px) {
    @include adaptivPaddinglg(0, 0, 30, 30, 0, 0, 25, 25);
  }
  .logo {
    @include adaptivWidthLg(121, 80);
    @include adaptivHeightlg(45, 30);
    @media (max-width: 768px) {
      @include adaptivWidthmd(121, 80);
    }
  }
  &__content {
    display: flex;
    align-items: center;
    &_item {
      margin-left: auto;
      @media (max-width: 768px) {
        display: none;
      }
      &-btn {
        background: none;
        @include adaptiv-fontlg(20, 16);
        @include adaptiv-lineHeightlg(24, 20);
        color: #fff;
        font-family: "Montserrat-medium";
        @include adaptivPaddinglg(40, 40, 25, 25, 20, 20, 20, 20);
      }
    }
  }
  .navigation-list-mobile {
    display: none;
    @media (max-width: 768px) {
      &.active {
        display: block;
        @include adaptivWidthmd(768, 320);
        width: 100%;
        height: 100vh;
        background: linear-gradient(
          270deg,
          rgba(25, 60, 180, 0.92) 0.53%,
          #3c31a7 67.81%
        );
        position: absolute;
        right: 0;
        top: 100%;
      }
      &-list {
        width: 100%;
        height: 80%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .navigation-list-btn {
          width: 100%;
          padding-top: 18px;
          padding-bottom: 18px;
          @include adaptiv-fontmd(20, 14);
          letter-spacing: 2px;
          color: #fff;
          background: none;
          &:hover {
            background: #92147c;
          }
        }
      }
    }
  }
  .menu-burger {
    display: none;
    position: absolute;
    top: 30%;
    right: 10%;
    transform: translate(50%, -50%);
    width: 30px;
    height: 50px;
    background: transparent;
    overflow: hidden;
    @media (max-width: 768px) {
      display: block;
      &-line {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-direction: column;
        &:first-child {
          margin-top: 20px;
          height: 2px;
          display: flex;
          background: #fff;
          position: absolute;
          left: 0;
          transform: rotate(0);
        }
        &:nth-child(2) {
          margin-top: 10px;
          height: 2px;
          display: flex;
          background: #fff;
          position: absolute;
          left: 0;
          transform: rotate(0);
        }
        &:last-of-type {
          margin-bottom: 0;
          height: 2px;
          display: flex;
          background: #fff;
          position: absolute;
          left: 0;
          transform: rotate(0);
        }
      }
      &.open {
        display: block;
      }
      &.close {
        .menu-burger-line {
          &:first-child {
            display: none;
          }
          &:nth-child(2) {
            transform: rotate(40deg);
            width: 30px;
          }
          &:last-of-type {
            transform: rotate(-40deg);
            margin-top: 10px;
            width: 30px;
          }
        }
      }
    }
  }
}
</style>